html, body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
  max-height: 100vh;
  max-width: 100vw;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center{
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 450px) {
  font-size: 12px;
}

.preload-images {
	background: url(/static/media/magnet.e056c1e5.svg) no-repeat -9999px -9999px;
  background: url(data:image/png;base64,) no-repeat -9999px -9999px,
        url(data:image/png;base64,) no-repeat -9999px -9999px,
        url(/static/media/check.dfaa3b7e.svg) no-repeat -9999px -9999px,
		    url(/static/media/cross.4a358148.svg) no-repeat -9999px -9999px,
		    url(data:image/png;base64,) no-repeat -9999px -9999px,
		    url(data:image/png;base64,) no-repeat -9999px -9999px,
		    url(data:image/png;base64,) no-repeat -9999px -9999px;
}
.App{
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-logo {
  -webkit-animation: app-logo-spin infinite 5s linear;
          animation: app-logo-spin infinite 5s linear;
}

.load-logo{
  -webkit-animation: app-logo-spin infinite 2s linear;
          animation: app-logo-spin infinite 2s linear;
}

@-webkit-keyframes app-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes app-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

